.boton {
  width: 300px;
  background-color: #1f2937;
  margin-left: 20px;
  margin-top: 10px;
  border: none;
  color: #e5e7eb;
  border-radius: 5px;
  height: 40px;
  font-family: "Roboto", sans-serif;
  font-weight: 10;
}
.boton:active {
  box-shadow: 0 0 2px #e5e7eb;
}

.boton2 {
  margin-top: 25px;
  margin-left: 50px;
  width: 200px;
  background-color: #1e7529;
  border: none;
  color: #e5e7eb;
  border-radius: 5px;
  height: 35px;
  font-family: "Roboto", sans-serif;
  font-weight: 10;
}
.boton2:active {
  box-shadow: 0 0 2px #e5e7eb;
}
@media screen and (max-width: 768px) {
  .boton {
    width: 170px;
    background-color: #1f2937;
    margin-left: 15px;
    margin-top: 1px;
    border: none;
    color: #e5e7eb;
    border-radius: 5px;
    height: 40px;
    font-family: "Roboto", sans-serif;
    font-weight: 10;
  }
}
@media only screen and (max-width: 767px) {
  .boton {
    width: 160px;
    background-color: #1f2937;
    margin-left: 25px;
    margin-top: 10px;
    border: none;
    color: #e5e7eb;
    border-radius: 5px;
    height: 40px;
    font-family: "Roboto", sans-serif;
    font-weight: 10;
  }
  .boton2 {
    font-size: 10px;
    width: 80px;
    background-color:  #1e7529;
    margin-left: -100px;
    margin-top: 1px;

    border: none;
    color: #e5e7eb;
    border-radius: 5px;
    height: 40px;
    font-family: "Roboto", sans-serif;
    font-weight: 10;
  }
}