.App-header {
  background-color: #0e1117;
  min-height: 100vh;
}
.contenedor-1 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenedor-2 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.txt-entrada {
  text-align: center;
  margin-top: 90px;
  margin-left: 40px;
  width: 220px;
  height: 30px;
  border-radius: 4px;
  border-style: none;
}
.conte {
  padding-top: 10px;
}
.table-style {
  margin-top: 40px;
  width: 1500px;
  height: 460px;
  overflow-x: auto;
}
.table-style2{
  margin-top: 40px;
  width: 1500px;
  height: 360px;
  overflow-x: auto;
}
@media screen and (max-width: 768px) {
  .App-header {
    background-color: #0e1117;
    min-height: 100vh;
  }
  .contenedor-1 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .txt-entrada {
    text-align: center;
    margin-top: 90px;
    margin-left: 10px;
    width: 150px;
    height: 35px;
    border-radius: 4px;
    border-style: none;
  }
  .conte {
    padding-top: 1px;
  }
  .contenedor-2 {
    padding-left: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .table-style {
    margin-top: 40px;
    width: 750px;
    height: 460px;
    overflow-x: auto;
  }
  .table-style2{
    margin-top: 40px;
    width: 750px;
    height: 360px;
    overflow-x: auto;
  }
}
.textArea1{
  width: 650px;
}
@media only screen and (max-width: 767px) {
  /* Estilos para dispositivos con un ancho m√°ximo de 767px */
  .App-header {
    background-color: #0e1117;
    min-height: 100vh;
  }
  .contenedor-1 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .txt-entrada {
    text-align: center;
    margin-top: 90px;
    width: 100px;
    height: 30px;
    border-radius: 4px;
    border-style: none;
  }
  .conte {
    padding-top: 20px;
  }
  .contenedor-2 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .table-style {
    margin-top: 40px;
    width: 380px;
    height: 460px;
    overflow-x: auto;
  }
  .table-style2{
    margin-top: 40px;
    width: 380px;
    height: 360px;
    overflow-x: auto;
    overflow-y: auto;
  }
  .textArea1{
    width: 260px;
  }
  .phonestyle{
    margin-bottom: 60px;
  }
  .h1style{
    font-size: 16px !important;
  }
}